import React from 'react';

import { Accordion, AccordionItem, AccordionItemButton, AccordionItemHeading, AccordionItemPanel, } from 'react-accessible-accordion';
import 'react-accessible-accordion/dist/fancy-example.css';
const axios = require('axios')


export default class List extends React.Component {
    constructor(props) {
        super(props)
    }
    componentDidMount() {
        console.log(this.props.match.params.id)
        this.state = {
            id: this.props.match.params.id,
            data: []
        }
        this.getApi()

    }
    getApi = async () => {
        let data = await axios.get(`https://cdn-api.co-vin.in/api/v2/appointment/sessions/calendarByDistrict?district_id=${this.state.id}&date=28-04-2021`)
        console.log(data.data.centers, 'get')
        if(data != null){

            this.setState({ data: data?.data.centers },()=>{
                console.log(this.state.data.length,'gettt')
            })
        }
    }
    checkAvilable = (sessions)=>{
        console.log(sessions,'sessions')
    }
    render() {
        if(this.state?.data?.length > 0){

            return (
                <Accordion allowMultipleExpanded>
        {this.state?.data.map((item) => (
            <AccordionItem key={item.center_id}>
                <AccordionItemHeading>
                    <AccordionItemButton>
                        {item.name}{" "}
    
                        <span className={item?.fee_type === 'Free' ?'feeType':'paidType' }>{item.fee_type}</span>
                        {/* <a className="feeType" href="https://selfregistration.cowin.gov.in/" target="_blank">register/login</a> */}
                    </AccordionItemButton>
                </AccordionItemHeading>
                <AccordionItemPanel>
                <table class="w3-table w3-bordered">
        <tr>
          <th>available capacity</th>
          <th>date</th>
          <th>Action</th>
        </tr>
        {
            item.sessions.map((val,i)=>{
              return(
    
        <tr key={i}>
          <td>{Math.round(val.available_capacity)}</td>
          <td>{val.date}</td>
          <td> <a  href="https://selfregistration.cowin.gov.in/" target="_blank">schedule</a></td>
        </tr>
              )  
            })
        }
      
      </table>
                </AccordionItemPanel>
            </AccordionItem>
        ))}
    </Accordion>
     
     
            )
        }else{
            return <p>not found</p>
        }
    }
}
